import Button from '@mui/material/Button';
import { hover } from '@testing-library/user-event/dist/hover';
import { useState } from "react";
import "./brain.css";
import Tooltip from '@mui/material/Tooltip';

const Brain=()=>{

    const [valX,setValx]=useState("");
    const [valF,setValF]=useState("");
    const [valX1,setValx1]=useState([]);
    const [valF1,setValF1]=useState([]);
    const[xii,setXii]=useState("");
    const[fii,setFii]=useState("");
    const[fxi,setFx]=useState("");
    const[fxi2,setFx2]=useState("");
    const[fxi3,setFx3]=useState("");
    const[fxi4,setFx4]=useState("");
    const [valFx,setValfx]=useState([]);
    const [valFx2,setValfx2]=useState([]);
    const [valFx3,setValfx3]=useState([]);
    const [valFx4,setValfx4]=useState([]);
    const[sum1,setSum]=useState();
    const[sum2,setSum1]=useState();
    const[sum3,setSum2]=useState();
    const[sum4,setSum3]=useState();
    const[sum5,setSum4]=useState();
    const[sum6,setSum5]=useState();
    const[mu1,setMu1]=useState();
    const[mu2,setMu2]=useState();
    const[mu3,setMu3]=useState();
    const[mu4,setMu4]=useState();
    const[mue1,setMue1]=useState();
    const[mue2,setMue2]=useState();
    const[mue3,setMue3]=useState();
    const[mue4,setMue4]=useState();
    const [color1,setColor1]=useState();
    const [color2,setColor2]=useState();
    const valChange=(event)=>{
       const name=event.target.name;
       const value= Math.round(event.target.value);
       if(name==='xi'){
        setValx(value);
       }
       else{
        setValF(value);
       }
    }
    // let sum=0;

    const subMe=()=>{
        setValx1((oldval)=>{
            return [...oldval,valX]
        })
        setValF1((oldval)=>{
            return [...oldval,valF]
        })
        setValx("");
        setValF("");
        setFii("fi");
        setXii("Xi");
        setValfx((oldval)=>{
            return[...oldval,(valX*valF)]
        })
        
        setValfx2((oldval)=>{
            return[...oldval,(valX*valF*valX)]
        })
        setValfx3((oldval)=>{
            return[...oldval,(valX*valF*valX*valX)]
        })
        setValfx4((oldval)=>{
            return[...oldval,(valX*valX*valX*valX*valF)]
        })
        setFx("fixi");
        setFx2("fi(xi)^2");
        setFx3("fi(xi)^3");
        setFx4("fi(xi)^4");
        
    }
    const subMe2=()=>{
        const som = valFx.reduce((accumulator, currentValue) => {
            return accumulator + currentValue;
          }, 0);
          setSum(som);
        const som1 = valFx2.reduce((accumulator, currentValue) => {
            return accumulator + currentValue;
          }, 0);
          setSum1(som1);
        const som2 = valFx3.reduce((accumulator, currentValue) => {
            return accumulator + currentValue;
          }, 0);
          setSum2(som2);
        const som3 = valFx4.reduce((accumulator, currentValue) => {
            return accumulator + currentValue;
          }, 0);
          setSum3(som3);
        const som4 = valX1.reduce((accumulator, currentValue) => {
            return accumulator + currentValue;
          }, 0);
          setSum4(som4);
            const som5 = valF1.reduce((accumulator, currentValue) => {
            return accumulator + currentValue;
          }, 0);
          setSum5(som5)

          const m1 =(som/som5).toFixed(2);
          const m2 =(som1/som5).toFixed(2);
          const m3 =(som2/som5).toFixed(2);
          const m4 =(som3/som5).toFixed(2);
          setSum5(som5);
          setMu1(m1);
          setMu2(m2);
          setMu3(m3);
          setMu4(m4);
          const a1= m1-m1;
          const a2= (m2-(m1**2)).toFixed(2);
          const a3= (m3-(m1*m2)-(m1*m2)-(m1*m2)+(m1**3)+(m1**3)).toFixed(2);
          const a4= (m4-(m3*m1)-(m3*m1)-(m3*m1)-(m3*m1)+(m2*(m1**2))+(m2*(m1**2))+(m2*(m1**2))+(m2*(m1**2))+(m2*(m1**2))+(m2*(m1**2))-(m1**4)-(m1**4)-(m1**4)).toFixed(2);

          setMue1(a1);
          setMue2(a2);
          setMue3(a3);
          setMue4(a4);

    }
    const hover=(event)=>{
        const name= event.target.name;
        if(name==='+'){
        setColor1("gray");
    }else{
        setColor2("gray");
    }
    }
    const hover1=(event)=>{
        const name= event.target.name;
        if(name==='+'){
        setColor1("white");
    }else{
        setColor2("white");
    }
        
    }

    return(
        <div>
            <div className="title">
                <input type='text' onChange={valChange} name='xi' value={valX} placeholder="Enter the value of Xi"></input>
                <input type='text' onChange={valChange} name='fi' value={valF} placeholder="Enter the value of Fi"></input>
                <Tooltip title="Add"><Button variant="outlined" onClick={subMe} className='btn' onMouseEnter={hover} onMouseLeave={hover1} name='+' style={{backgroundColor:`${color1}`,margin:'10px'}}>+</Button></Tooltip>
                <Tooltip title="Find Values"><Button variant="outlined" onClick={subMe2} className='btn' onMouseEnter={hover} onMouseLeave={hover1} name='submit' style={{backgroundColor:`${color2}`}}>Submit </Button></Tooltip>
            </div>

                <div className="values"><div><li className="head1">{xii}</li>{valX1.map((val,index)=>{
                   return( <li>{val}</li>)
                })}<li className="ans">{sum5}</li></div>
                <div><li className="head1">{fii}</li>
                {valF1.map((val,index)=>{
                    return( <li>{val}</li>)               
                     })}<li className="ans">{sum6}</li></div>
                <div><li className="head1">{fxi}</li>
                {valFx.map((val,index)=>{
                    return( <li>{val}</li>)               
                     })}<li className="ans">{sum1}</li></div>
                <div><li className="head1">{fxi2}</li>
                {valFx2.map((val,index)=>{
                    
                    return(<li>{val}</li>  
                    
                    )
                    })}<li className="ans">{sum2}</li>
                    </div>
                <div><li className="head1">{fxi3}</li>
                {valFx3.map((val,index)=>{

                    return( <li>{val}</li>)               
                     })}<li className="ans">{sum3}</li></div>
                <div><li className="head1">{fxi4}</li>
                {valFx4.map((val,index)=>{
                    return( <li>{val}</li>)               
                     })}<li className="ans">{sum4}</li></div>
                     </div>
                     <div className="val">
                <ol>
                    <p><strong>&#956;<sub>1</sub></strong> = {mue1}</p>
                    <p><strong>&#956;<sub>2</sub></strong> = {mue2}</p>
                    <p><strong>&#956;<sub>3</sub></strong> = {mue3}</p>
                    <p><strong>&#956;<sub>4</sub></strong> = {mue4}</p>
                    </ol>
                <ol>
                    <p><strong>&#956;'<sub>1</sub></strong>={mu1}</p>
                    <p><strong>&#956;'<sub>2</sub></strong>={mu2}</p>
                    <p><strong>&#956;'<sub>3</sub></strong>={mu3}</p>
                    <p><strong>&#956;'<sub>4</sub></strong>={mu4}</p>
                </ol>
                </div>
                <div>
                <p style={{fontWeight:'bold',marginLeft:'40px'}}>Mean=<span style={{fontWeight:'lighter'}}>{mu1}</span></p>
                <p style={{fontWeight:'bold',marginLeft:'40px'}}>Sd=<span style={{fontWeight:'lighter'}}>{mue2}</span></p>
                </div>
        </div>
    )

    
}

export default Brain;